body {
  background-color: #f2f2f2;
  color: @color-title;
  font-family: @main-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin: 0;

  @media (min-width: @desktop-width) {
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
